import { auth } from '@/auth';
import ContentCard from '@/components/ContentCard';
import { mockedContent } from '@/lib/data';
import dbConnect from '@/lib/db';
import User from '@/models/User';
import { redirect } from 'next/navigation';
import { BookmarkX } from 'lucide-react';

export default async function SavedPage() {
  const session = await auth();
  if (!session?.user?.email) redirect('/login');

  await dbConnect();
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const user: any = await User.findOne({ email: session.user.email });
  const savedIds: string[] = user?.savedContentIds || [];

  const savedContent = mockedContent.filter(item => savedIds.includes(item.id));

  return (
    <div className="pt-4 pb-4 px-1">
      <header className="mb-6 px-4">
         <h1 className="text-2xl font-bold text-accent-green mb-2">My Stash</h1>
         <p className="text-gray-400 text-sm">Things you actually wanted to remember.</p>
      </header>

      {savedContent.length > 0 ? (
        <div className="flex flex-col gap-2">
            {savedContent.map((item) => (
                <ContentCard
                    key={item.id}
                    {...item}
                    isSaved={true}
                />
            ))}
        </div>
      ) : (
        <div className="flex flex-col items-center justify-center h-[60vh] text-center px-6">
            <BookmarkX className="w-16 h-16 text-gray-800 mb-4" />
            <h3 className="text-xl font-bold text-gray-700 mb-2">Nothing here yet</h3>
            <p className="text-gray-500 text-sm">Go explore the world and save some juicy facts!</p>
        </div>
      )}
    </div>
  );
}
